html {
  box-sizing: border-box;
  font-size: 16px;
  font-family: sans-serif;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

a {
  margin-right: 1rem;
}

hr {
  margin: 2rem auto;
}

.active {
  font-weight: bold;
  color: #61dafb;
}

input[type="text"] {
  border: thin solid #dedede;
  border-radius: 0.25rem;
  padding: 0.75rem;
  margin-bottom: 1rem;
  outline: none;
  display: block;
  width: 100%;
  font-size: 1rem;
  line-height: 1;
  background-color: transparent;
}

button,
input[type="button"],
input[type="submit"],
input[type="email"],
textarea {
  border: thin solid #444;
  border-radius: 0.25rem;
  padding: 0.75rem;
  margin-bottom: 1rem;
  display: block;
  background-color: transparent;
  color: #444;
  font-weight: bold;
  font-size: 1rem;
  line-height: 1;
  outline: none;
  width: 100%;
 
}

input[type="submit"] {
  cursor: pointer;
}

input[type="button"] {
  cursor: pointer;
}


textarea {
  font-family: inherit;
  resize: none;
}

input[type="button"]:disabled {
  border-color: rgba(118,118,118,.3);
  color: rgba(16, 16, 16, .3);
  background-color: rgba(239, 239, 239, .3);
  cursor: default;
}

form {
  margin-bottom: 1rem;
}

hr {
  margin: 2rem auto;
}

table {
border-spacing: 0;
border-collapse: collapse;
width: 100%;  
}

th,
td {
  text-align: left;
  padding: 0.5rem;
  border-bottom: thin solid #dedede;
}

@media screen and (min-width: 1024px) {
  .grid-1-2 {
      display: grid;
      grid-template-columns: 1fr 1fr;
      align-items: start;
      gap: 2rem 
  }
  .grid-1-3 {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      align-items: start;
      gap: 2rem 
  }
}